.navbar {
	position: fixed;
	top: 0;
	width: 100%;
	height: 25px;
	padding: 20px;
	background-color: #111;
	display: flex;
	justify-content: space-between;
}
.logo {
	position: fixed;
	left: 20px;
	width: 80px;
}
.avatar {
	position: fixed;
	right: 20px;
	width: 20px;
}


.nav-tabs{
	position: fixed;
	left: 140px;
	display: flex;
	list-style: none;
	color: #fff;
}

.nav-tabs li{
	cursor: pointer;
	font-size: 15px;
	font-weight: bold;
	margin: 0px 10px;
}
* {
	margin: 0;
	padding: 0;
}

body {
	font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	background-color: #111;
}

.banner {
	background-size: cover;
	height: 448px;
	color: white;
}

.content {
	padding: 140px 0px 0px 70px;
	height: 190px;
}

.title {
	font-size: 3rem;
	font-weight: 800;
	padding-bottom: 0.3rem;
	text-shadow:#111;
}

.button {
	color: white;
	outline: none;
	border: none;
	font-weight: 700;
	border-radius: 5px;
	padding-left: 2rem;
	padding-right: 2rem;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	background-color: rgba(51, 51, 51, 0.5);
	cursor: pointer;
	margin-right: 1rem;
}

.button:hover {
	color: black;
	background-color: #e6e6e6;
}

.description {
	width: 45rem;
	line-height: 1.3;
	padding-top: 1rem;
	font-size: 1rem;
	height: 80px;
	text-shadow: #111;
	max-width: 600px;
}

.fade-bottom {
	height: 7.4rem;
	background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, .61),#111);
}

.row {
	color: #fff;
	padding: 20px;
}

.genre{
	margin-bottom: 10px;
}

.posters {
	display: flex;
	overflow-x: scroll;
	overflow-y: hidden;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster {
	max-height: 200px;
	margin-right: 10px;
}

.small-poster{
    max-height: 100px;
    max-width: 100%;
	margin-right: 10px;
}

.poster, .small-poster{
	cursor: pointer;
}

.poster:hover , .small-poster:hover{
	-webkit-transform: scale(1.08);
	        transform: scale(1.08);
	transition-duration: 0.1s;
}

.trailer{
	margin-top: 20px;
}
